// Menu Button
.menu-btn {
    position: absolute;
    z-index: 3;
    right: 35px;
    top: 35px;
    cursor: pointer;
    @include easeOut;

    .btn-line {
        width: 28px;
        height: 3px;
        margin: 0 0 5px 0;
        background: #fff;
        @include easeOut;
    }

    // rotate into X with menu lines
    &.close {
        -webkit-transform: rotate(180deg); /* Safari */
        transform: rotate(180deg);
        transition: transform 1s;
        .btn-line {
            &:nth-child(1) {
                transform: rotate(45deg) translate(5px, 5px)
            }
            &:nth-child(2) {
                opacity: 0;
            }
            &:nth-child(3) {
                transform: rotate(-45deg) translate(7px, -6px)
            }
        }
    }
}

// menu overlay
.menu {
    position: fixed;
    top: 0;
    width: 100%;
    opacity: 0.9;
    visibility: hidden;

    &.show {
        visibility: visible;
    }

    &-branding,
    &-nav {
        display: flex;
        flex-flow: column wrap;
        align-items: center;
        justify-content: center;
        float: left;
        width: 50%;
        height: 100vh;
        overflow: hidden;
    }

    &-nav {
        padding: 0;
        background: darken($primary-color, 5);
        list-style: none;
        transform: translate3d(0, -100%, 0);
        @include easeOut;

        &.show {
            // slide in from top
            transform: translate3d(0, 0, 0);
            transition: transform 1s;
        }
    }

    // branding side
    &-branding {
        background: $primary-color;
        transform: translate3d(0, 100%, 0);
        @include easeOut;

        &.show {
            // slide in from bottom
            transform: translate3d(0, 0, 0);
            transition: transform 1s;
        }
    
    .portrait {
        width: 250px;
        height: 250px;
        background: url('../../images/background-image.jpg');
        border-radius: 50%;
        border: solid 3px $secondary-color;
    }
    }

    .nav-item {
        transform: translate3d(600px, 0, 0);
        @include easeOut;

        &.show {
            // slide in from right
            transform: translate3d(0, 0, 0);
            transition: transform 1s;
        }

        &.current > a {
            color: $secondary-color;
        }
    }

    .nav-link {
        display: inline-block;
        position: relative;
        font-size: 30px;
        text-transform: uppercase;
        padding: 1rem 0;
        font-weight: 300;
        color: #fff;
        text-decoration: none;
        @include easeOut;

        &:hover {
            color: $secondary-color;
        }
    }

    // delay each nav item slide
@for $x from 1 through 4 {
    .nav-item:nth-child(#{$x}) {
        transition-delay: $x * 0.1s;
    }
}
}

